// 获取卡牌数量
export const getCardCount = (cardType: string) => {
    const gameConfig = read();
    let cardCount = 0;

    for (const tempCard of gameConfig.deck[cardType]) {
        if (tempCard.count != 0) {
            cardCount += 1;
        }
    }

    return cardCount;
}

// 获得卡组剩余数量
export const getDeckListByCount = (deckType: string) => {
    const gameConfig = read();
    const deckList = [];

    for (let i = 0; i < gameConfig.deck[deckType].length; i++) {
        const card = gameConfig.deck[deckType][i];
        deckList.push(card);
    }

    return deckList;
}

// 特殊卡牌处理机制 ↓
export const specialCard = (card : any) => {
    const gameConfig = read();

    // 特殊卡牌
    // ---------------------------------

    // 资本主义
    if (card.name == "Capitalism") {
        deleteCard(card);

        ElMessage({
            message: '获得你序号+2和序号-2玩家的一半货币（5号玩家则1号和3号获得，6号玩家则2号和4号获得',
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 生财有道
    if (card.name == "Make-Wealth") {
        deleteCard(card);

        ElMessage({
            message: '货币 +6',
            grouping: true,
            showClose: true,
            duration: 0
        });

        const gameConfig = read();
        gameConfig.money += 6;
        playerMoney.value = gameConfig.money;
        save(gameConfig);
    }

    // 起手换牌
    if (card.name == "Change-Card") {
        deleteCard(card);
        const gameConfig = read();

        let cardNum = 0;
        let drawsCount = 0;

        for (let i = 0; i < deckType.length; i++) {

            const deckList = gameConfig.deckList[deckType[i]];

            for (let j = 0; j < deckList.length; j++) {
                deleteCard(deckList[j]);
                cardNum += 1;
                drawsCount += 1;
            }

        }

        gameConfig.drawCount += drawsCount;
        save(gameConfig);

        drawCardCount.value = gameConfig.drawCount;
        ElMessage({
            message: "回收了 " + cardNum + " 张卡牌 已兑换成 " + drawsCount + " 次抽卡机会",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 恶魔契约
    if (card.name == "Devils-Pact") {
        deleteCard(card);
        const gameConfig = read();
        gameConfig.devilspact = 2;
        save(gameConfig);

        ElMessage({
            message: "每在商店购买一次装备就获得一次抽卡机会（至多触发两次）",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 上贡
    if (card.name == "Tribute") {
        deleteCard(card);

        ElMessage({
            message: "从你的增益卡牌中挑选一张送给1号玩家（如果是1号，则给2号一张）",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 决斗
    if (card.name == "Duel") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: "你与" + players[0] + "号玩家签订决斗协议，你们立即前往私人熔炉竞技场的生存使用当前拥有的武器技能" +
                                                    "决斗，获得第一个回合胜利的玩家得到失败者的一半货币，不许认输！",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 等价交换
    if (card.name == "Equivalent-Exchange") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: "你需要和 " + players[0] + "号 玩家进行所有卡牌交换",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 有福同享
    if (card.name == "Blessed-To-Share") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: "你需要和 " + players[0] + "号 玩家进行增益卡牌共享",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 有难同当
    if (card.name == "Share-The-Difficulties") {
        deleteCard(card);

        do {
            players = shuffle(players).value;
        } while (players[0] == gameConfig.roleId);

        ElMessage({
            message: "你需要和 " + players[0] + "号 玩家进行减益卡牌共享",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }

    // 微弱不适

    // 强烈不适

    // 卧槽我钱包呢
    if (card.name == "Lost-Wallet") {
        deleteCard(card);

        const gameConfig = read();
        gameConfig.money = 0;
        save(gameConfig);

        playerMoney.value = gameConfig.money;
        ElMessage({
            message: "杂鱼~你的货币全没了哦~",
            grouping: true,
            showClose: true,
            duration: 0
        });
    }
}
